/**
 * 设置处理cookies的方法
 */

/**
 * 设置cookie
 * @param c_name cookie名称
 * @param value cookie值
 * @param expireMinutes 过期时间
 * （若不设过期时间， cookie会在浏览器关闭时清除）
 */
export const setCookie = (c_name: string, value: string, expireMinutes?: number, path?: string) => {
  const exDate = new Date()
  if (expireMinutes) {
    exDate.setTime(exDate.getTime() + expireMinutes * 60 * 1000)
    document.cookie = `${c_name}=${value}${expireMinutes ? ';expires=' + exDate.toUTCString() : ''};path=${path ?? '/'};`
  } else {
    document.cookie = `${c_name}=${value};path=${path ?? '/'};`
  }
}
//获取cookie
export const getCookie = (name: string) => {
  // 获取所有的 cookie 字符串
  const cookies = document.cookie
  // 分割成多个 cookie 对
  const cookiesArray = cookies.split('; ')

  for (let i = 0; i < cookiesArray.length; i++) {
    const cookie = cookiesArray[i]
    // 分割 key 和 value
    const cookieParts = cookie.split('=')
    // 如果找到匹配的 cookie 名称，则返回其值
    if (cookieParts[0] === name) {
      return decodeURIComponent(cookieParts[1])
    }
  }
  // 如果没有找到匹配的 cookie，则返回 null 或者空字符串
  return null
}
//删除cookie
export const delCookie = (key: string, domain?: string) => {
  const date = new Date()
  date.setTime(date.getTime() - 1)
  const delValue = getCookie(key)
  if (!!delValue) {
    document.cookie =
      key +
      '=' +
      delValue +
      ';expires=' +
      date.toUTCString() +
      `${domain ? ';domain=' + domain : ''}`
    document.cookie =
      key +
      '=' +
      delValue +
      ';path=/;expires=' +
      date.toUTCString() +
      `${domain ? ';domain=' + domain : ''}`
  }
}

// 删除所有cookie
export const delAllCookie = () => {
  const keys = document.cookie.match(/[^ =;]+(?==)/g)
  if (keys) {
    for (let i = keys.length; i--; ) {
      // 要分别区分 path 与 指定domain下的cookie过期，不然会删不干净
      document.cookie = keys[i] + '=0;path=/;expires=' + new Date(0).toUTCString()
      document.cookie = keys[i] + '=0;expires=' + new Date(0).toUTCString()
      // 删除存储在主域名下的cookie
      // TODO: 需要修改为实际的域名:domain=.xx.com
      document.cookie =
        keys[i] + '=0;path=/;expires=' + new Date(0).toUTCString() + ';domain=.xx.com'
      document.cookie = keys[i] + '=0;expires=' + new Date(0).toUTCString() + ';domain=.xx.com'
    }
  }
}
