// IE 兼容性 polyfills
import 'core-js/stable'
import 'regenerator-runtime/runtime'

// 基础类型声明
declare global {
  interface Window {
    console: Console
  }
}

// 修复 console 对象（IE8 及以下版本）
if (typeof window !== 'undefined' && !window.console) {
  window.console = {
    log: () => {},
    error: () => {},
    warn: () => {},
    info: () => {},
    debug: () => {},
  } as Console
}
