import { createRouter, createWebHashHistory } from 'vue-router'
import type { RouteRecordRaw } from 'vue-router'
import { LOGIN_COOKIE_TOKEN, SYSTEM_CONFIG } from '@/constants/global'
import { ROUTER_PATH } from '@/constants/menu'
// import ManagementLayout from '../layouts/ManagementLayout.vue'
import BasicLayout from '../layouts/BasicLayout.vue'
import { getCookie } from '@/utils/cookie'
import type { RouteLocationNormalized } from 'vue-router'

const routes: RouteRecordRaw[] = [
  {
    path: '/',
    component: BasicLayout,
    children: [
      {
        path: ROUTER_PATH.patientInfo,
        name: 'PatientInfo',
        component: () => import('../views/patient/PatientInfo.vue'),
        meta: { title: '患者信息' },
      },
      {
        path: ROUTER_PATH.patientDetail + '/:id/:name',
        name: 'PatientDetail',
        component: () => import('../views/patient/patientDetail/PatientDetail.vue'),
        meta: {
          title: (route: RouteLocationNormalized) => `病患详情：${route.params.name || '未知患者'}`,
          dynamicTitle: true,
        },
      },
      {
        path: ROUTER_PATH.patientRecords,
        name: 'PatientRecords',
        component: () => import('../views/patient/PatientRecords.vue'),
        meta: { title: '病历管理' },
      },
      {
        path: ROUTER_PATH.patientAppointments,
        name: 'PatientAppointments',
        component: () => import('../views/patient/PatientAppointments.vue'),
        meta: { title: '预约管理' },
      },
      {
        path: ROUTER_PATH.surgeryPlan,
        name: 'SurgeryPlan',
        component: () => import('../views/surgery/SurgeryPlan.vue'),
        meta: { title: '手术计划' },
      },
      {
        path: ROUTER_PATH.surgeryRecords,
        name: 'SurgeryRecords',
        component: () => import('../views/surgery/SurgeryRecords.vue'),
        meta: { title: '手术记录' },
      },
      {
        path: ROUTER_PATH.surgeryFollowup,
        name: 'SurgeryFollowup',
        component: () => import('../views/surgery/SurgeryFollowup.vue'),
        meta: { title: '术后跟踪' },
      },
      {
        path: ROUTER_PATH.nursingPlan,
        name: 'NursingPlan',
        component: () => import('../views/nursing/NursingPlan.vue'),
        meta: { title: '护理计划' },
      },
      {
        path: ROUTER_PATH.nursingRecords,
        name: 'NursingRecords',
        component: () => import('../views/nursing/NursingRecords.vue'),
        meta: { title: '护理记录' },
      },
      {
        path: ROUTER_PATH.nursingAssessment,
        name: 'NursingAssessment',
        component: () => import('../views/nursing/NursingAssessment.vue'),
        meta: { title: '护理评估' },
      },
      {
        path: ROUTER_PATH.systemUsers,
        name: 'SystemUsers',
        component: () => import('../views/system/SystemUsers.vue'),
        meta: { title: '用户管理' },
      },
      {
        path: ROUTER_PATH.systemPermissions,
        name: 'SystemPermissions',
        component: () => import('../views/system/SystemPermissions.vue'),
        meta: { title: '权限管理' },
      },
      {
        path: ROUTER_PATH.systemBackup,
        name: 'SystemBackup',
        component: () => import('../views/system/SystemBackup.vue'),
        meta: { title: '数据备份' },
      },
      {
        path: ROUTER_PATH.welcome,
        name: 'Welcome',
        component: () => import('../views/HomeView.vue'),
        meta: { title: '首页' },
      },
    ],
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/login/Login.vue'),
    meta: { title: '登录' },
  },
  // {
  //   path: '/:pathMatch(.*)*',
  //   redirect: '/welcome',
  // },
]

const router = createRouter({
  history: createWebHashHistory('/mERPweb'),
  routes,
})

// 全局前置守卫
router.beforeEach((to, from, next) => {
  // 设置页面标题
  const title = to.meta.title
  if (title) {
    document.title = `${title}${SYSTEM_CONFIG.TITLE_SEPARATOR}${SYSTEM_CONFIG.TITLE}`
  } else {
    document.title = SYSTEM_CONFIG.TITLE
  }

  // 获取cookie中的token
  const token = getCookie(LOGIN_COOKIE_TOKEN)

  // 如果访问的不是登录页且没有token,则跳转到登录页
  if (to.path !== '/login' && !token) {
    next('/login')
    return
  }

  // 如果访问登录页且有token,则跳转到首页
  if (to.path === '/login' && token) {
    next('/')
    return
  }

  next()
})

export default router
