import { MockMethod } from 'vite-plugin-mock'
import { Random } from 'mockjs'

// 生成随机患者数据
const generatePatient = (id: string) => ({
  id,
  name: Random.cname(),
  age: Random.integer(18, 80),
  gender: Random.pick(['男', '女']),
  operatingRoom: Random.pick(['04手术间', '13手术间', '14手术间', '15手术间']),
  bloodType: Random.pick(['A型', 'B型', 'O型', 'AB型']),
  bedNumber: `${Random.integer(1, 99)}床`,
  surgeryStatus: Random.pick(['pending', 'in_progress', 'completed']),
  surgeryTime: Random.datetime('yyyy-MM-dd HH:mm'),
  handOverStatus: Random.pick(['pending', 'completed']),
  doctorName: Random.cname() + '医生',
  nursingLevel: Random.pick(['一级护理', '二级护理', '三级护理']),
  applicationNumber: Random.string('number', 7),
})

// 生成患者列表
const generatePatientList = (count: number) => {
  const list = []
  for (let i = 0; i < count; i++) {
    const id = `2306${Random.string('number', 4)}`
    list.push(generatePatient(id))
  }
  return list
}

// 模拟数据
const patientList = generatePatientList(20)

export default [
  {
    url: '/api/patient/list',
    method: 'post',
    response: ({ body }) => {
      let list = [...patientList]

      // 根据查询参数过滤
      if (body) {
        const {
          operatingRoom,
          surgeryTimeStart,
          surgeryTimeEnd,
          surgeryStatus,
          handOverStatus,
          searchType,
          searchValue,
        } = body

        // 手术间过滤
        if (operatingRoom) {
          list = list.filter((item) => item.operatingRoom.includes(operatingRoom))
        }

        // 手术时间范围过滤
        if (surgeryTimeStart) {
          list = list.filter((item) => item.surgeryTime >= surgeryTimeStart)
        }
        if (surgeryTimeEnd) {
          list = list.filter((item) => item.surgeryTime <= surgeryTimeEnd)
        }

        // 手术状态过滤
        if (surgeryStatus) {
          list = list.filter((item) => item.surgeryStatus === surgeryStatus)
        }

        // 交接状态过滤
        if (handOverStatus) {
          list = list.filter((item) => item.handOverStatus === handOverStatus)
        }

        // 搜索类型和值过滤
        if (searchType && searchValue) {
          list = list.filter((item) => {
            const value = item[searchType]
            return value && value.toString().toLowerCase().includes(searchValue.toLowerCase())
          })
        }
      }

      return {
        code: 200,
        success: true,
        data: {
          total: list.length,
          list,
        },
        message: '获取成功',
      }
    },
  },
  {
    url: '/api/patient/detail/:id',
    method: 'get',
    response: ({ query }) => {
      const patient = patientList.find((item) => item.id === query.id)

      if (patient) {
        return {
          code: 200,
          success: true,
          data: patient,
          message: '获取成功',
        }
      }

      return {
        code: 404,
        message: '患者不存在',
      }
    },
  },
] as MockMethod[]
