import { defineStore } from 'pinia'
import { ref } from 'vue'
import { getPatientList } from '@/api/patient'

export interface PatientDetailTab {
  // 患者ID
  [id: string]: {
    id: string // 患者ID
    name: string // 患者姓名
    title: string // 标签页标题
    type: string // 标签页类型：'basic' | 'surgery' | 'nursing' | 'records'
  }[]
}

export interface ActiveTab {
  patientId: string
  patientName: string
  tabId: string
}

export const usePatientInfoStore = defineStore('patientInfo', () => {
  // 搜索表单
  const searchForm = ref({
    operatingRoom: '',
    surgeryTimeStart: '',
    surgeryTimeEnd: '',
    surgeryStatus: '',
    handOverStatus: '',
    searchType: '',
    searchValue: '',
  })

  // 加载状态
  const loading = ref(false)

  // 病患列表
  const patientList = ref<Patient_API.PatientInfo[]>([])

  // 患者标签页列表 患者ID -> 标签页列表
  const detailTabs = ref<PatientDetailTab>({})

  // 当前激活的: 患者ID、 患者姓名、 标签页ID
  const activeTab = ref<ActiveTab>({
    patientId: '',
    patientName: '',
    tabId: '',
  })

  // 设置当前激活的标签页
  const setActiveTab = (patientId: string, patientName: string, tabId: string) => {
    activeTab.value = {
      patientId,
      patientName,
      tabId,
    }
  }
  // 添加标签页
  const addDetailTab = (
    patientId: string,
    patientName: string,
    tabInfo: { id: string; title: string },
  ) => {
    const existingTab = detailTabs.value[patientId]?.find((tab) => tab.id === tabInfo.id)

    if (!existingTab) {
      detailTabs.value[patientId] = detailTabs.value[patientId] || []
      detailTabs.value[patientId].push({
        id: tabInfo.id,
        name: patientName,
        title: `${patientName}-${tabInfo.title}`,
        type: tabInfo.id,
      })
    }

    setActiveTab(patientId, patientName, tabInfo.id)
  }

  // 关闭标签页
  const closeDetailTab = (patientId: string, tabId: string) => {
    const index = detailTabs.value[patientId]?.findIndex((tab) => tab.id === tabId)
    if (index > -1) {
      detailTabs.value[patientId].splice(index, 1)
      // 如果关闭的是当前标签页，切换到最后一个标签页
      if (tabId === activeTab.value.tabId && detailTabs.value[patientId].length > 0) {
        setActiveTab(
          patientId,
          detailTabs.value[patientId][detailTabs.value[patientId].length - 1].name,
          detailTabs.value[patientId][detailTabs.value[patientId].length - 1].id,
        )
      }
    }
  }

  // 关闭所有标签页
  const closeAllDetailTabs = (patientId: string) => {
    delete detailTabs.value[patientId]
  }

  // 获取病患列表
  const fetchPatientList = async () => {
    loading.value = true
    try {
      const res = await getPatientList(searchForm.value as Patient_API.SearchParams)
      if (res.success) {
        patientList.value = res.data.list || []
      }
    } catch (error) {
      console.error('获取病患列表失败:', error)
    } finally {
      loading.value = false
    }
  }

  // 重置搜索表单
  const resetSearchForm = () => {
    searchForm.value = {
      operatingRoom: '',
      surgeryTimeStart: '',
      surgeryTimeEnd: '',
      surgeryStatus: '',
      handOverStatus: '',
      searchType: '',
      searchValue: '',
    }
  }

  return {
    searchForm,
    loading,
    patientList,
    detailTabs,
    activeTab,
    setActiveTab,
    addDetailTab,
    closeDetailTab,
    closeAllDetailTabs,
    fetchPatientList,
    resetSearchForm,
  }
})
