import { MockMethod } from 'vite-plugin-mock'
import { Random } from 'mockjs'

const surgeryList = Array.from({ length: 15 }, (_, index) => ({
  id: index + 1,
  patientId: Random.integer(1, 20),
  patientName: Random.cname(),
  surgeryType: Random.pick(['白内障手术', '青光眼手术', '近视矫正手术', '眼底手术']),
  surgeryDate: Random.datetime('yyyy-MM-dd'),
  surgeryTime: Random.datetime('HH:mm:ss'),
  surgeon: Random.cname(),
  assistants: Array.from({ length: Random.integer(1, 3) }, () => Random.cname()),
  status: Random.pick(['待手术', '手术中', '已完成', '已取消']),
  operatingRoom: Random.pick(['手术室1', '手术室2', '手术室3']),
  notes: Random.paragraph(1, 3),
  createTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
  updateTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
}))

export default [
  {
    url: '/api/surgery/list',
    method: 'get',
    response: ({ query }) => {
      const { page = 1, pageSize = 10, status = '', patientName = '' } = query
      const list = surgeryList.filter(
        (item) =>
          (status ? item.status === status : true) &&
          (patientName ? item.patientName.includes(patientName) : true),
      )

      return {
        code: 200,
        data: {
          total: list.length,
          list: list.slice((page - 1) * pageSize, page * pageSize),
        },
        message: '获取成功',
      }
    },
  },
  {
    url: '/api/surgery/detail',
    method: 'get',
    response: ({ query }) => {
      const { id } = query
      const surgery = surgeryList.find((item) => item.id === Number(id))

      if (surgery) {
        return {
          code: 200,
          data: surgery,
          message: '获取成功',
        }
      }
      return {
        code: 404,
        message: '手术记录不存在',
      }
    },
  },
  {
    url: '/api/surgery/add',
    method: 'post',
    response: ({ body }) => {
      const newSurgery = {
        id: surgeryList.length + 1,
        ...body,
        createTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
        updateTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
      }
      surgeryList.push(newSurgery)

      return {
        code: 200,
        data: newSurgery,
        message: '添加成功',
      }
    },
  },
  {
    url: '/api/surgery/update',
    method: 'put',
    response: ({ body }) => {
      const index = surgeryList.findIndex((item) => item.id === body.id)
      if (index > -1) {
        surgeryList[index] = {
          ...surgeryList[index],
          ...body,
          updateTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
        }
        return {
          code: 200,
          data: surgeryList[index],
          message: '更新成功',
        }
      }
      return {
        code: 404,
        message: '手术记录不存在',
      }
    },
  },
  {
    url: '/api/surgery/delete',
    method: 'delete',
    response: ({ query }) => {
      const { id } = query
      const index = surgeryList.findIndex((item) => item.id === Number(id))
      if (index > -1) {
        surgeryList.splice(index, 1)
        return {
          code: 200,
          message: '删除成功',
        }
      }
      return {
        code: 404,
        message: '手术记录不存在',
      }
    },
  },
  {
    url: '/api/surgery/stats',
    method: 'get',
    response: () => {
      return {
        code: 200,
        data: {
          total: surgeryList.length,
          pending: surgeryList.filter((item) => item.status === '待手术').length,
          inProgress: surgeryList.filter((item) => item.status === '手术中').length,
          completed: surgeryList.filter((item) => item.status === '已完成').length,
          cancelled: surgeryList.filter((item) => item.status === '已取消').length,
          surgeryTypes: {
            cataract: surgeryList.filter((item) => item.surgeryType === '白内障手术').length,
            glaucoma: surgeryList.filter((item) => item.surgeryType === '青光眼手术').length,
            myopia: surgeryList.filter((item) => item.surgeryType === '近视矫正手术').length,
            fundus: surgeryList.filter((item) => item.surgeryType === '眼底手术').length,
          },
        },
        message: '获取成功',
      }
    },
  },
] as MockMethod[]
