import { MockMethod } from 'vite-plugin-mock'
import { Random } from 'mockjs'

const nursingList = Array.from({ length: 30 }, (_, index) => ({
  id: index + 1,
  patientId: Random.integer(1, 20),
  patientName: Random.cname(),
  nurseId: Random.integer(1, 10),
  nurseName: Random.cname(),
  type: Random.pick(['术前护理', '术中护理', '术后护理', '日常护理']),
  level: Random.pick(['一级护理', '二级护理', '三级护理']),
  status: Random.pick(['待执行', '执行中', '已完成', '已终止']),
  startTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
  endTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
  content: Random.paragraph(1, 3),
  notes: Random.paragraph(1, 2),
  createTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
  updateTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
}))

export default [
  {
    url: '/api/nursing/list',
    method: 'get',
    response: ({ query }) => {
      const { page = 1, pageSize = 10, type = '', status = '', patientName = '' } = query
      const list = nursingList.filter(
        (item) =>
          (type ? item.type === type : true) &&
          (status ? item.status === status : true) &&
          (patientName ? item.patientName.includes(patientName) : true),
      )

      return {
        code: 200,
        data: {
          total: list.length,
          list: list.slice((page - 1) * pageSize, page * pageSize),
        },
        message: '获取成功',
      }
    },
  },
  {
    url: '/api/nursing/detail',
    method: 'get',
    response: ({ query }) => {
      const { id } = query
      const nursing = nursingList.find((item) => item.id === Number(id))

      if (nursing) {
        return {
          code: 200,
          data: nursing,
          message: '获取成功',
        }
      }
      return {
        code: 404,
        message: '护理记录不存在',
      }
    },
  },
  {
    url: '/api/nursing/add',
    method: 'post',
    response: ({ body }) => {
      const newNursing = {
        id: nursingList.length + 1,
        ...body,
        createTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
        updateTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
      }
      nursingList.push(newNursing)

      return {
        code: 200,
        data: newNursing,
        message: '添加成功',
      }
    },
  },
  {
    url: '/api/nursing/update',
    method: 'put',
    response: ({ body }) => {
      const index = nursingList.findIndex((item) => item.id === body.id)
      if (index > -1) {
        nursingList[index] = {
          ...nursingList[index],
          ...body,
          updateTime: Random.datetime('yyyy-MM-dd HH:mm:ss'),
        }
        return {
          code: 200,
          data: nursingList[index],
          message: '更新成功',
        }
      }
      return {
        code: 404,
        message: '护理记录不存在',
      }
    },
  },
  {
    url: '/api/nursing/delete',
    method: 'delete',
    response: ({ query }) => {
      const { id } = query
      const index = nursingList.findIndex((item) => item.id === Number(id))
      if (index > -1) {
        nursingList.splice(index, 1)
        return {
          code: 200,
          message: '删除成功',
        }
      }
      return {
        code: 404,
        message: '护理记录不存在',
      }
    },
  },
  {
    url: '/api/nursing/stats',
    method: 'get',
    response: () => {
      return {
        code: 200,
        data: {
          total: nursingList.length,
          pending: nursingList.filter((item) => item.status === '待执行').length,
          inProgress: nursingList.filter((item) => item.status === '执行中').length,
          completed: nursingList.filter((item) => item.status === '已完成').length,
          terminated: nursingList.filter((item) => item.status === '已终止').length,
          nursingTypes: {
            preOperation: nursingList.filter((item) => item.type === '术前护理').length,
            inOperation: nursingList.filter((item) => item.type === '术中护理').length,
            postOperation: nursingList.filter((item) => item.type === '术后护理').length,
            daily: nursingList.filter((item) => item.type === '日常护理').length,
          },
          nursingLevels: {
            level1: nursingList.filter((item) => item.level === '一级护理').length,
            level2: nursingList.filter((item) => item.level === '二级护理').length,
            level3: nursingList.filter((item) => item.level === '三级护理').length,
          },
        },
        message: '获取成功',
      }
    },
  },
] as MockMethod[]
