import request from '@/utils/request'

export const getPatientList = (params: Patient_API.SearchParams = {}) => {
  return request.post<Patient_API.GetPatientListResponse>('/api/patient/list', { params })
}

export const getPatientDetail = (id: string) => {
  return request.post<Common_API.Response<Patient_API.PatientInfo>>(`/api/patient/detail/${id}`)
}
