import { defineStore } from 'pinia'
import { ref } from 'vue'
import { setCookie, delCookie, getCookie } from '../utils/cookie'
import { LOCAL_STORAGE_KEY, LOGIN_COOKIE_TOKEN } from '@/constants/global'
import { ROUTER_PATH } from '@/constants/menu'
import { useRouter } from 'vue-router'
import { loginApi } from '@/api/login'
import { deleteStorage, getStorage, setStorage } from '@/utils/storage'

export const useUserStore = defineStore('user', () => {
  // 全局信息，先从缓存中读取
  const userInfo = ref<Login_API.UserInfo | null>(getStorage(LOCAL_STORAGE_KEY.userInfo))
  const token = ref<string>(getCookie(LOGIN_COOKIE_TOKEN) || '')
  const operatingRoom = ref<string>(getStorage(LOCAL_STORAGE_KEY.operatingRoom) as string)
  const router = useRouter()

  // 设置用户信息
  const setUserInfo = (info: Login_API.UserInfo) => {
    console.log('Setting user info:', info)
    userInfo.value = info
    setStorage(LOCAL_STORAGE_KEY.userInfo, info)
  }

  // 设置token
  const setToken = (newToken: string) => {
    token.value = newToken
    setCookie(LOGIN_COOKIE_TOKEN, newToken)
  }

  // 设置手术室
  const setOperatingRoom = (room: string) => {
    operatingRoom.value = room
    setStorage(LOCAL_STORAGE_KEY.operatingRoom, room)
  }

  // 登录
  const login = async (username: string, password: string, operatingRoom: string) => {
    try {
      // 调用登录API
      const res = await loginApi({ username, password, operatingRoom })
      console.log('Login response:', res)

      if (res.code === 200 && res.data) {
        console.log('res.data', res.data)
        const userData = {
          id: res.data.id,
          username: res.data.username,
          name: res.data.name,
          roles: res.data.roles,
          token: res.data.token,
        }
        // 存储用户信息
        setUserInfo(userData)
        // 存储token
        setToken(res.data.token)
        // 存储当前手术室
        setOperatingRoom(operatingRoom)
        return { success: true }
      }

      return { success: false, errorMsg: res.message || '登录失败' }
    } catch (error) {
      console.error('登录失败:', error)
      return { success: false, errorMsg: '登录失败，请稍后重试' }
    }
  }

  // 退出登录
  const logout = () => {
    // 清除用户信息
    userInfo.value = null
    // 清除token
    token.value = ''
    delCookie(LOGIN_COOKIE_TOKEN)
    // 清除存储
    deleteStorage(LOCAL_STORAGE_KEY.userInfo)
    deleteStorage(LOCAL_STORAGE_KEY.operatingRoom)
    // 跳转到登录页
    router.push(ROUTER_PATH.login)
  }

  // 初始化用户信息
  const initUserInfo = () => {
    const savedToken = getCookie(LOGIN_COOKIE_TOKEN)
    const savedUserInfo = getStorage(LOCAL_STORAGE_KEY.userInfo)
    console.log('Initializing user info:', { savedToken, savedUserInfo })

    if (savedToken && savedUserInfo) {
      token.value = savedToken
      userInfo.value = savedUserInfo as Login_API.UserInfo
    }
  }

  return {
    userInfo,
    token,
    operatingRoom,
    setUserInfo,
    setToken,
    setOperatingRoom,
    login,
    logout,
    initUserInfo,
  }
})
