import { MockMethod } from 'vite-plugin-mock'

export default [
  {
    url: '/api/user/login',
    method: 'post',
    response: ({ body }) => {
      console.log('Mock login handler called', body)
      const { username, password } = body

      if (username && password) {
        const token = 'mock-token-' + Date.now()
        return {
          code: 200,
          data: {
            id: username,
            username: username,
            name: '测试用户',
            roles: ['user'],
            token: token,
          },
          message: '登录成功',
        }
      }

      return {
        code: 401,
        message: '工号或密码错误',
      }
    },
  },
] as MockMethod[]
